.project {
    width: 350px;
    background-color: lightgray;
    display: flex;
    flex-direction: column;
}

.img {
    width: 100%;
    height: 200px;
    background-color: #637e94;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button {
    text-transform: uppercase;
    background-color: gold;
    border-radius: 5px;
    width: 120px;
    height: 30px;
}

.content {
    display: flex;
    flex-direction: column;
    background-color: lightgray;
    align-items: flex-start;
    padding: 5px;
}

.title {
    background-color: #94727c;
    margin: 0 0 5px 0;
}


.description {
    display: flex;
    background-color: #94727c;
}
